<template>
  <div id="app">
    <div id='banner' class='padded'>
      <img src='./assets/SMF_A_plain@3x.png'>        
    </div>
    <div id="macro-bg">
      <div id='chart-header'>
          <h1>
          Macro Overview
          </h1>
      </div> 
      <div v-if="!loading" id='chart-container'>
        
        <div v-for="key in Object.keys(data)" :key="key">
          <LineChart class='chart' v-bind:category='key.split("_")[0]' v-bind:title='key.split("_").slice(1,).join(" ")' v-bind:data='data[key]'></LineChart>
        </div>
      </div>
    </div>

    <div id='chart-header'>
      <h1>
        Risk Dashboard  
      </h1> 
    </div>

    <div v-if="!loading_risk"  class='padded'>
      <h2>
        Portfolio Summary
      </h2>
      <table>
        <th>Alpha</th>
        <th>Beta</th>
        <th>Sharpe</th>
        <th>Tracking Error (%)</th>
        <tr>
          <td>{{risk.portfolio_summary_stats.alpha}}</td>
          <td>{{risk.portfolio_summary_stats.beta}}</td>
          <td>{{risk.portfolio_summary_stats.sharpe}}</td>
          <td>{{risk.portfolio_summary_stats.tracking_error_pct}}</td>  
        </tr>
      </table>
      
      <h2>
        Region Attribution
      </h2>
      <table>
        <th>Region</th>
        <th>Benchmark Total Return</th>
        <th>Portfolio Total Return</th>
        <th>Portfolio Outperformance</th>
        <th>Selection Effect</th>
        <th>Allocation Effect</th>
        <th>Total Effect</th>
        <tr v-for="[region, r_data] in Object.entries(risk.attribution.regions)" :key="region">
          <td>{{region}}</td>
          <td>{{r_data["Benchmark Total Return"]}}</td>
          <td>{{r_data["Portfolio Total Return"]}}</td>  
          <td>{{r_data["Portfolio Outperformance"]}}</td>  
          <td>{{r_data["Selection Effect"]}}</td>  
          <td>{{r_data["Allocation Effect"]}}</td>  
          <td>{{r_data["Total Effect"]}}</td>  
        </tr>
      </table>
      
      <h2>
        Sector Attribution
      </h2>
      <table>
        <th>Sector</th>
        <th>Allocation Effect (EU)</th>
        <th>Allocation Effect (US)</th>
        <th>Selection Effect (EU)</th>
        <th>Selection Effect (US)</th>
        <th>Total Effect (EU)</th>
        <th>Total Effect (US)</th>
        <tr v-for="[sector, s_data] in Object.entries(risk.attribution.sectors)" :key="sector">
          <td>{{sector}}</td>
          <td>{{roundToTwo(s_data["Allocation_EU"])}}</td>
          <td>{{roundToTwo(s_data["Allocation_US"])}}</td>  
          <td>{{roundToTwo(s_data["Selection_EU"])}}</td>  
          <td>{{roundToTwo(s_data["Selection_US"])}}</td>
          <td>{{roundToTwo(s_data["Allocation_EU"] + s_data["Selection_EU"])}}</td> 
          <td>{{roundToTwo(s_data["Allocation_US"] + s_data["Selection_US"])}}</td> 
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import LineChart from "./components/LineChart.vue";
import axios from 'axios';
import * as d3 from "d3";



export default {
  name: "App",
  components: {
    LineChart
  },
  data() {
    return {
      data: {},
      loading: true,
      risk: {},
      loading_risk: true,
    }
  },
  methods: {
    roundToTwo: function(num) {    
      return +(Math.round(num + "e+2")  + "e-2");
    }
  },
  created() {
        // TODO Revert for production mode!!!!! 
        const path_chart = './chart_data'
        // const path_chart = 'http://localhost:5000/chart_data';
        
        axios.get(path_chart)
            .then((res) => {
                console.log("Recieved data...")
                this.data = res.data

                var parseDate = d3.timeParse("%Y-%m-%d");

                Object.keys(this.data).forEach((key) => {
                  this.data[key].forEach(function(d) {
                    d.date = parseDate(d.date);
                  });
                });
                
                console.log("Recieved data...")
                this.loading = false
            })
            .catch((error) => {
                // eslint-disable-next-line
                console.error(error);
            }); 

        // TODO Revert for production mode!!!!! 
        const path_risk = './risk_data'
        // const path_risk = 'http://localhost:5000/risk_data';
        axios.get(path_risk)
            .then((res) => {
                console.log("Recieved data...")
                this.risk = res.data
                this.loading_risk = false
            })
            .catch((error) => {
                // eslint-disable-next-line
                console.error(error);
            }); 
    },
};
</script>

